<script>
import { ref, onMounted, watch } from "vue"
import { useRouter } from "vue-router"
import { Toast } from "../../components/global/swal"
import Header from "../../components/global/header/HeaderArea2.vue"
import DiseaseType from "../../components/global/DiseaseType.vue"
import { errorHandle } from "../../utils/handle"
import {
  apiGetResumeInfo,
  apiPutResumeInfo,
  apiGetWorkHourType,
  apiGetExperience,
  apiGetCityArea,
  apiUpload,
} from "@/api/api"
import { useCityStore, useExperienceStore } from "@/store/global"
import { getUrlId } from "@/utils/handle"
import { focusInput } from "@/utils/fixFooter"
// import { ExtCall } from "@/utils/extCall"

export default {
  setup() {
    const cityStore = useCityStore()
    const experienceStore = useExperienceStore()
    const router = useRouter()
    const urlId = getUrlId().urlId
    const urlIdLink = `/resume/info/${urlId}`
    const data = ref()
    let isShowTimeInput = ref(0) //顯示其它工作時段
    const showTimeInput = () => {
      if (isShowTimeInput.value == 0) {
        isShowTimeInput.value = 1
      } else {
        data.value.otherJobTime = 0
        isShowTimeInput.value = 0
      }
    }

    // 控制頁面
    let stepStyle = ref(["nav-link active", "nav-link", "nav-link", "nav-link"])
    let showPage = ref("page1")
    let title = ref("基本資料")
    const form1 = ref(null)
    const save = (e) => {
      e.preventDefault()
      if (form1.value.reportValidity()) {
        showPage.value = "page2"
        stepStyle.value = [
          "nav-link visited",
          "nav-link active",
          "nav-link",
          "nav-link",
        ]
        title.value = "工作地點"
      }
    }
    const save2 = () => {
      if (
        data.value.selectArea.length == 0 &&
        data.value.selectAllCityId.length == 0
      ) {
        Toast("請至少選擇一個工作地點")
      } else {
        showPage.value = "page3"
        stepStyle.value = [
          "nav-link visited",
          "nav-link visited",
          "nav-link active",
          "nav-link",
        ]
        title.value = "照護經驗(複選)"
      }
    }
    const save3 = () => {
      if (data.value.diseaseId.length == 0) {
        Toast("請至少選擇一個照護經驗")
      } else {
        showPage.value = "page4"
        stepStyle.value = [
          "nav-link visited",
          "nav-link visited",
          "nav-link visited",
          "nav-link active",
        ]
        title.value = "相關證照"
      }
    }
    const save4 = async () => {
      data.value.seniorityId = data.value.seniority.id
      data.value.servicePlace = []
      data.value.servicePlaceId.forEach((item) =>
        data.value.servicePlace.push(parseInt(item))
      )
      console.log(data.value)
      let msg = await apiPutResumeInfo({ id: urlId }, data.value)
      if (msg.status == true) {
        Toast("修改成功")
        router.push({ path: `/resume/info/${urlId}` })
      } else {
        let errMessage = errorHandle(msg)
        Toast(errMessage)
      }
    }
    const backToPage1 = () => {
      showPage.value = "page1"
      stepStyle.value = ["nav-link active ", "nav-link", "nav-link", "nav-link"]
      title.value = "基本資料"
    }
    const backToPage2 = () => {
      showPage.value = "page2"
      stepStyle.value = [
        "nav-link visited ",
        "nav-link active",
        "nav-link",
        "nav-link",
      ]
      title.value = "工作地點"
    }
    const backToPage3 = () => {
      showPage.value = "page3"
      stepStyle.value = [
        "nav-link visited",
        "nav-link visited",
        "nav-link visited",
        "nav-link active",
      ]
      title.value = "照護經驗"
    }
    //page2
    const checkAll = (val) => {
      let areaList = []
      if (data.value.selectAllCityId.includes(val.id)) {
        // console.log('全選')
        val.areas.forEach((x) => data.value.selectArea.push(x.id))
      } else {
        // console.log('取消全選')
        val.areas.forEach((x) => areaList.push(x.id))
        // console.log(111, areaList)
        data.value.selectArea = data.value.selectArea.filter((e) => {
          return areaList.indexOf(e) === -1
        })
      }
    }
    const cancelAll = (item, item2, $event) => {
      if ($event.target.checked == false) {
        // console.log("取消全選")
        data.value.selectAllCityId = data.value.selectAllCityId.filter(
          (x) => x != item.id
        )
      }
      // console.log("data.value.selectAllCityId",data.value.selectAllCityId)
    }
    //page3
    //取病症代碼
    const updateDiseaseData = (val) => {
      data.value.diseaseId = val
      // console.log(data.value.diseaseId)
    }
    //page4
    //檔案上傳
    const myUploadFile = ref(null)
    const myUploadFile2 = ref(null)
    const myUploadFile3 = ref(null)
    const myUploadFile4 = ref(null)

    const useIdCheckFileSize = async (id, requestData) => {
      //檢查上傳檔案大小
      const fileInput = document.getElementById(`formFile${id}`)
      const getFile = fileInput?.files?.[0]
      if (!getFile) return false

      const fileSize = getFile?.size / 1024 / 1024
      if (fileSize > process.env.VUE_APP_UPLOAD_LIMIT) {
        fileInput.value = ""
        Toast(`圖片檔案大小上限為${process.env.VUE_APP_UPLOAD_LIMIT}MB`)
        return false
      }
      const response = await apiUpload(requestData)
      if (response.status) {
        // 成功回拋 response
        return response
      } else {
        fileInput.value = ""
        const errorMessage = response.message || "上傳失敗"
        Toast(errorMessage)
        return false
      }
    }

    const handleFileUpload = async (licenseId, UploadFile) => {
      //   try {
      let requestData = new FormData()
      requestData.append("singleFile", UploadFile.files[0])
      requestData.append("uploadType", "ResumeArea")
      requestData.append("jobType", data.value.jobTypeId)

      if (licenseId == 1) {
        const response = await useIdCheckFileSize(1, requestData)
        if (!response) return
        data.value.license.mainLicensePhotoId = response.data.id
      } else if (licenseId == 2) {
        const response = await useIdCheckFileSize(2, requestData)
        if (!response) return
        data.value.license.healthFormPhotoId = response.data.id
      } else if (licenseId == 3) {
        const response = await useIdCheckFileSize(3, requestData)
        if (!response) return
        data.value.license.policeCriminalRecordId = response.data.id
      } else {
        const response = await useIdCheckFileSize(4, requestData)
        if (!response) return
        data.value.license.otherLicensePhotoId = response.data.id
      }
      //   } catch (error) {
      //     console.log("error", error)
      //   }
    }
    const openImg = (imgUrl) => {
      Toast({
        imageUrl: imgUrl,
        imageWidth: 400,
        imageHeight: 200,
        imageAlt: "圖片預覽",
      })
    }

    let workHourType = ref()
    let experienceType = ref()
    let cityArea = ref()
    onMounted(async () => {
      focusInput()
      try {
        data.value = await apiGetResumeInfo({ id: urlId })
        data.value = data.value.data
        //取工作時段
        workHourType.value = await apiGetWorkHourType({
          jobTypeId: data.value.jobTypeId,
        })
        workHourType.value = workHourType.value.data.data
        isShowTimeInput.value = data.value.otherJobTime == 0 ? 0 : 1
        data.value.workPeriodId = []
        data.value.timePeriod.forEach((item) => {
          data.value.workPeriodId.push(item.id)
        })
        if (isShowTimeInput.value == 1) {
          data.value.otherJobTimeId = ["work99"]
        }
        //服務年資列表
        if (experienceStore.experienceTypeList == undefined) {
          // call api
          experienceType.value = await apiGetExperience()
          experienceType.value = experienceType.value.data.data
          experienceStore.experienceTypeList = experienceType.value
        } else {
          experienceType.value = experienceStore.experienceTypeList
        }
        //服務地點
        data.value.servicePlaceId = []
        data.value.servicePlace.forEach((item) =>
          data.value.servicePlaceId.push(item.id)
        )
        //取縣市地區
        //取縣市地區
        if (cityStore.cityAreaList == undefined) {
          // console.log('沒有')
          // call api
          cityArea.value = await apiGetCityArea()
          cityArea.value = cityArea.value.data
          cityStore.cityAreaList = cityArea.value
        } else {
          // console.log('有')
          cityArea.value = cityStore.cityAreaList
        }

        console.log(data.value)
      } catch (error) {
        console.log(error)
      }
    })

    return {
      data,
      stepStyle,
      showPage,
      title,
      save,
      save2,
      save3,
      save4,
      backToPage1,
      backToPage2,
      backToPage3,
      workHourType,
      experienceType,
      cityArea,
      form1,
      checkAll,
      cancelAll,
      updateDiseaseData,
      openImg,
      handleFileUpload,
      myUploadFile,
      myUploadFile2,
      myUploadFile3,
      myUploadFile4,
      urlId,
      urlIdLink,
      isShowTimeInput,
      showTimeInput,
    }
  },
  components: {
    Header,
    DiseaseType,
  },
}
</script>

<template>
  <Header v-if="showPage == 'page1'" :path="urlIdLink" />
  <Header v-if="showPage == 'page2'" :clickFun="backToPage1" />
  <Header v-if="showPage == 'page3'" :clickFun="backToPage2" />
  <Header v-if="showPage == 'page4'" :clickFun="backToPage3" />

  <div class="resumeAdd">
    <div class="mb-3 step-container">
      <ul class="nav justify-content-center">
        <!-- 已完成 visited，目前 active -->
        <li class="nav-item">
          <a :class="stepStyle[0]"><span>1</span></a>
        </li>
        <li class="nav-item">
          <a :class="stepStyle[1]"><span>2</span></a>
        </li>
        <li class="nav-item">
          <a :class="stepStyle[2]"><span>3</span></a>
        </li>
        <li class="nav-item">
          <a :class="stepStyle[3]"><span>4</span></a>
        </li>
      </ul>
    </div>
    <div class="title">
      <p>{{ title }}</p>
    </div>
    <hr />
    <div class="page1" v-show="showPage == 'page1'" v-if="data?.jobTypeIdName">
      <form ref="form1">
        <div class="mb-3">
          <label class="form-label">履歷身分<span class="star">*</span></label>
          <div class="main" v-if="showPage == 'page1'">
            <div class="input-group">
              <select class="col form-control form-select" disabled>
                <option :value="data.jobTypeId">
                  {{ data.jobTypeIdName }}
                </option>
              </select>
            </div>
          </div>
        </div>
        <!-- <div class="mb-3">
                    <label class="form-label">工作時段<span class="star">*</span></label>
                    <div class="input-list">
                        <div class="form-check-inline" v-for="item in workHourType" :key="item.id">
                            <input type="radio" class="form-check-input" :value="item.id" :id="'work' + item.id"
                                v-model="data.timePeriod[0].id">
                            <label class="form-check-label" :for="'work' + item.id">{{ item.name }}</label>
                        </div>
                    </div>
                </div> -->
        <div class="mb-3">
          <label class="form-label">工作時段<span class="star">*</span></label>
          <div class="input-list">
            <div
              class="form-check-inline"
              v-for="item in workHourType"
              :key="item.id"
            >
              <input
                type="checkbox"
                class="form-check-input"
                :value="item.id"
                :id="'work' + item.id"
                v-model="data.workPeriodId"
              />
              <label class="form-check-label" :for="'work' + item.id">{{
                item.name
              }}</label>
            </div>
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="work99"
                id="work99"
                @change="showTimeInput"
                v-model="data.otherJobTimeId"
              />
              <label class="form-check-label" for="work99">其他</label>
            </div>
            <input
              type="number"
              class="form-control mt-1"
              placeholder="請輸入其他工作時間"
              v-model="data.otherJobTime"
              min="1"
              max="24"
              v-if="isShowTimeInput"
            />
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">服務年資<span class="star">*</span></label>
          <div class="input-list">
            <div
              class="form-check-inline"
              v-for="item in experienceType"
              :key="item.id"
            >
              <input
                type="radio"
                class="form-check-input"
                :value="item.id"
                :id="'exp' + item.id"
                v-model="data.seniority.id"
              />
              <label class="form-check-label" :for="'exp' + item.id">{{
                item.name
              }}</label>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">工作地點<span class="star">*</span></label>
          <div class="input-list">
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="1"
                id="live1"
                v-model="data.servicePlaceId"
              />
              <label class="form-check-label" for="live1">居家</label>
            </div>
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="2"
                id="live2"
                v-model="data.servicePlaceId"
              />
              <label class="form-check-label" for="live2">醫院</label>
            </div>
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="3"
                id="live3"
                v-model="data.servicePlaceId"
              />
              <label class="form-check-label" for="live3">療養院</label>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label"
            >可開始工作日期<span class="star">*</span></label
          >
          <input
            type="date"
            class="form-control"
            v-model="data.startDate"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(居家/小時)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入居家/小時"
            v-model="data.wage.home"
            min="100"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(醫院/小時)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入醫院/小時"
            v-model="data.wage.hospital"
            min="100"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(居家/日)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入時薪"
            v-model="data.wage.fullDay.home"
            min="100"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(醫院/日)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入時薪"
            v-model="data.wage.fullDay.hospital"
            min="100"
            required
          />
        </div>
      </form>
    </div>
    <div class="page1" v-show="showPage == 'page2'" v-if="data?.jobTypeIdName">
      <div class="mb-3">
        <label class="form-label">工作地點</label>
        <div class="accordion" id="accordionDisease">
          <div
            class="accordion-item"
            v-for="(item, index) in cityArea"
            :key="item.id"
          >
            <div class="accordion-header">
              <button
                class="accordion-button collapsed"
                type="button"
                data-bs-toggle="collapse"
                :data-bs-target="'#collapse' + (index + 100)"
              >
                {{ item.city_name }}
              </button>
            </div>
            <div
              :id="'collapse' + (index + 100)"
              class="accordion-collapse collapse"
              data-bs-parent="#accordionDisease"
            >
              <div class="accordion-body">
                <div class="input-list">
                  <div class="form-check-inline">
                    <input
                      type="checkbox"
                      class="form-check-input"
                      :value="item.id"
                      :id="item.id"
                      v-model="data.selectAllCityId"
                      @change="checkAll(item)"
                    />
                    <label class="form-check-label" :for="item.id">全區</label>
                  </div>
                  <br />
                  <div
                    class="form-check-inline"
                    v-for="item2 in item.areas"
                    :key="item2.id"
                  >
                    <input
                      type="checkbox"
                      class="form-check-input"
                      :value="item2.id"
                      :id="item2.id"
                      v-model="data.selectArea"
                      @change="cancelAll(item, item2, $event)"
                    />
                    <label class="form-check-label" :for="item2.id">{{
                      item2.area_name
                    }}</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="page1" v-show="showPage == 'page3'" v-if="data?.jobTypeIdName">
      <DiseaseType
        @disease-data="updateDiseaseData"
        :disease_p="data.diseaseId"
      />
    </div>
    <div class="page1" v-show="showPage == 'page4'" v-if="data?.jobTypeIdName">
      <div class="mb-3">
        <label class="form-label">證書(照)</label>
        <input
          type="file"
          class="form-control"
          id="formFile1"
          @change="handleFileUpload(1, myUploadFile)"
          ref="myUploadFile"
          accept="image/png, image/jpeg"
          required
        />
        <span class="fileNameTitle">已上傳檔案：</span>
        <a
          class="fileName"
          v-if="data.license?.mainLicensePhotoId"
          @click="openImg(data.license?.mainLicensePhotoUrl)"
          >{{ data.license.mainLicensePhotoName }}</a
        >
      </div>
      <div class="mb-3">
        <label class="form-label">證號</label>
        <input
          type="text"
          class="form-control"
          v-model="data.license.certificateCode"
        />
      </div>
      <div class="mb-3">
        <label class="form-label">一年內體檢表</label>
        <input
          type="file"
          class="form-control"
          id="formFile2"
          @change="handleFileUpload(2, myUploadFile2)"
          ref="myUploadFile2"
          accept="image/png, image/jpeg"
          required
        />
        <span class="fileNameTitle">已上傳檔案：</span>
        <a
          class="fileName"
          @click="openImg(data.license?.healthFormPhotoUrl)"
          v-if="data.license?.healthFormPhotoId"
          >{{ data.license?.healthFormPhotoName }}</a
        >
      </div>
      <div class="mb-3">
        <label class="form-label">良民證</label>
        <input
          type="file"
          class="form-control"
          id="formFile3"
          @change="handleFileUpload(3, myUploadFile3)"
          ref="myUploadFile3"
          accept="image/png, image/jpeg"
          required
        />
        <span class="fileNameTitle">已上傳檔案：</span
        ><a
          class="fileName"
          @click="openImg(data.license?.policeCriminalRecordUrl)"
          v-if="data.license?.policeCriminalRecordId"
          >{{ data.license?.policeCriminalRecordName }}</a
        >
      </div>
      <div class="mb-3">
        <label class="form-label">其他證照</label>
        <input
          type="file"
          class="form-control"
          id="formFile4"
          @change="handleFileUpload(4, myUploadFile4)"
          ref="myUploadFile4"
          accept="image/png, image/jpeg"
          required
        />
        <span class="fileNameTitle">已上傳檔案：</span>
        <a
          class="fileName"
          @click="openImg(data.license.otherLicensePhotoUrl)"
          v-if="data.license?.otherLicensePhotoId"
          >{{ data.license?.otherLicensePhotoName }}</a
        >
      </div>
    </div>
  </div>
  <footer class="c-footer">
    <div class="ui-tabbtn">
      <div class="row">
        <div class="col">
          <button
            class="btn btn-primary"
            type="button"
            @click="save"
            v-if="showPage == 'page1'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save2"
            v-if="showPage == 'page2'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save3"
            v-if="showPage == 'page3'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save4"
            v-if="showPage == 'page4'"
          >
            完成新增
          </button>
        </div>
      </div>
    </div>
  </footer>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";

Header {
  background-color: #ffffff;
}

.resumeAdd {
  margin-bottom: calc($footer-height + 15px);
  margin-top: $header-height;
  padding: 1.25rem;
}

.title {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
}

.fileNameTitle {
  font-size: 0.8rem;
  color: #909090;
}

.fileName {
  font-size: 0.8rem;
}
</style>
